<template>
	<view class="calendar">
		<view class="sort row bothSide verCenter">
			<view class="sort-wrap row">
				<text @click="tab(index)" :class="{ active: active == index }" v-for="(item, index) in itemtext" :key="index">{{ item }}</text>
			</view>
			<text style="color: #E1E1E1;margin-right: 29rpx;font-size: 40rpx;">|</text>
			<view class="time row verCenter" @click="open">
				<text>{{ starTime || '选择时间' }}</text>
				<icon class="ico-1"></icon>
			</view>
		</view>
		<!-- <view class="nav row boxsiz">
			<text class="active">全部</text>
			<text>文艺</text>
			<text>体育</text>
			<text>娱乐</text>
			<text>旅游</text>
		</view> -->
		<view class="list column boxsiz">
			<template v-if="showCalendar.length > 0">
				<navigator  hover-class="none" :url="'/pages/singer/vocalConcert?id='+item.id" class="box row bothSide boxsiz" v-for="(item, index) in showCalendar" :key="index">
					<view class="pic"><image lazy-load="true" fade-show="true" :src="item.picture_and_vedio[0].url" mode="aspectFill"></image></view>
					<view class="con">
						<text class="t1">{{ item.title }}</text>
						<text class="t2">{{ item.start_time }}</text>
						<text class="t3">{{ item.address }}</text>
						<view class="text row bothSide verCenter">
							<view class="text-price">
								<text class="t1a">￥</text>
								<text class="t1b">{{ item.price }}</text>
							</view>
						</view>
					</view>
				</navigator>
			</template>
			<template v-else>
				<view class="no-date column rowCenter">
					<view class="bg"></view>
					<text>暂无演出</text>
				</view>
			</template>
		</view>
		<uni-calendar ref="calendar" :insert="false" @confirm="confirm" />
	</view>
</template>

<script>
import { API } from '@/util/api.js';
export default {
	data() {
		return {
			active: 0,
			showCalendar: [],
			type: 1,
			starTime: '',
			itemtext: ['七日精选', '月度看点', '全部时间']
		};
	},
	onLoad(options) {
		this.getData();
	},
	onPullDownRefresh() {
		this.refresh();
	},
	methods: {
		refresh() {
			uni.stopPullDownRefresh();
			this.getData();
		},
		getData() {
			this.request(API.showCalendar, 'GET', { type: this.type, starTime: this.starTime }, true).then(res => {
				if (res.code === 10000) {
					this.showCalendar = res.data;
				} else {
				}
			});
		},
		tab(index) {
			this.active = index;
			this.type = index + 1;
			this.getData();
		},
		open() {
			this.$refs.calendar.open();
		},
		confirm(e) {
			console.log(e);
			this.starTime = e.fulldate;
			this.type=4;
			this.getData();
		},
		bindDateChange: function(e) {
			this.date = e.detail.value;
		}
	}
};
</script>

<style scoped lang="scss">
@import '../../assets/css/index/calendar.scss';
</style>
